body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.card {
  background-color: #ffffffe0;
  border-radius: 8px;
  box-shadow: 0 1px 3px rgba(60,64,67, 0.3), 0 4px 8px 3px rgba(60,64,67, 0.15);
  margin: 0 auto;
  padding: 20px;
  position: relative;
  top: 25vh;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  width: 30%;
}

.top {
  top: 50px;
}

.flexed, .form {
  display: flex;
  flex-wrap: wrap;
}

@media (max-width: 767px) {
  .card {
    top: 10vh;
    width: 60%;
  }
  .top {
    top: 50px;
  }
  .form {
    display: block;
  }
}
.container {
  margin-right: 5px;
  margin-bottom: 10px;
  width: 90%;
}

.label {
  color: #808080;
  padding-bottom: 5px;
}

.input {
  border-radius: 3px;
  border: 1px solid #e5e5e5;
  font-size: 16px;
  padding: 10px 15px;
  width: -webkit-fill-available;
}

.input:focus {
  outline: none;
  border: 1px solid #db7093;
}

.icon {
  position: absolute;
  padding: 5px 0px;
}

@media (max-width: 767px) {
  .container {
    width: 100%
  }
}
.button {
  border-radius: 5px;
  cursor: pointer;
  padding: 10px 15px;
  margin: 0px 5px;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  font-weight: 500;
}

.primary {
  background-color: #db7093;
  color: #fff;
}

.secondary {
  background-color: #00000000;
  color: #fff;
  border: 2px solid #fff;
}

.third {
  background-color: #fff;
  color: #db7093
}
.Login {
  background: url(/idea-hunt-frontend/static/media/bg2.033aa169.jpg) top;
  background-size: cover;
  height: calc(100vh - 84px);
  position: absolute;
  width: 100%;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}
.link {
  text-decoration: none;
  cursor: pointer;
  color: black;
}
.main {
  padding: 10px 5%;
}

.item-container {
  line-height: 20px;
  margin: 20px;
  margin-bottom: 20px;
  border-bottom: 0.5px solid #d7d7d7;
}

h2 {
  font-weight: 400;
}

.body-preview {
  max-height: 40px;
  overflow: hidden;
}

.error-margin {
  margin: 10px 20px;
}

.error {
  padding: 10px 20px;
  color: #b72121;
  background-color: #fbaeae;
  border-radius: 5px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.metadata {
  padding-right: 20px;
  padding-top: 15px;
}

.author {
  font-weight: 500;
  font-size: 16px;
}

.date {
  font-size: 14px;
  font-weight: 300;
}

.right-date {
  float: right;
}

.upvote {
  float: right;
  display: flex;
  font-size: 20px;
}

.upvote-icon {
  height: 35px;
  margin: -5px;
}

.upvote-text {
  padding-left: 5px;
}

.thin {
  font-weight: 500;
}

.comment-section {
  margin: 20px;
}

.comment-container {
  border-bottom: 0.5px solid #d7d7d7;
  padding: 10px 0px;
}

.comment-author {
  font-size: 18px;
  margin-bottom: 5px;
}

@media (max-width: 767px) {
  .main {
    padding: 0px;
  }
}
.editor-input {
  border: 0px;
  font-size: 16px;
  padding: 10px 15px;
  font-family: Arial;
  width: -webkit-fill-available;
  margin: 1.7rem 1rem 0.15rem;
}

.editor-title {
  color: #1c1e29;
  letter-spacing: -.5px;
  font-weight: 700;
  font-size: 1.75rem;
}

.editor-input:focus {
  outline: none;
  border: 0px;
}

.button-container {
  display: flex;
  margin: 1rem;
}
.user-card {
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 1px 3px rgba(60,64,67, 0.3), 0 4px 8px 3px rgba(60,64,67, 0.15);
  margin: 0 auto;
  padding: 20px;
  padding-top: 5px;
  margin-top: 20px;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  width: 75%;
}

.user-title {
  font-weight: 500;
  text-align: center;
}

.center {
  text-align: center;
  padding-top: 10px;
  font-size: 18px;
}

.detail-container {
  display: flex;
}

.field {
  width: 50%;
}

.user-button {
  margin: 15px;
}
.header {
  display: flex;
  width: 100%;
  background-color: #db7093;
  color: #fff;
}

.logo {
  width: 50px;
  height: 50px;
  margin: 15px;
}

.title {
  font-weight: 500;
}

.right {
  position: absolute;
  right: 0;
  margin: 19px;
  display: flex;
}

.user-icon {
  border-radius: 50%;
  height: 35px;
  margin-right: 5px;
  border: 3px solid #fff;
}

.seperator {
  border: 0.5px solid white;
  margin: -18px 10px;
}

@media (max-width: 767px) {
  .title {
    display: none;
  }
}

@media (max-width: 400px) {
  .user-icon {
    display: none;
  }
  .seperator {
    margin: -18px 5px;
  }
}
.App {
  height: 100vh;
}
